.DrawerToggle {
    width: 45px;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: 9px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
}

.DrawerToggle div {
    width: 90%;
    height: 4px;
    border-radius: 100px;
    background-color: white;
}

@media (min-width: 501px) {
    .DrawerToggle {
        display: none;
    }
}
@media (max-width: 500px) {
    .DrawerToggle {
        width: 40px !important;
    }
}


